input::-webkit-input-placeholder{
    color:rgb(109, 106, 106);
}
.container{
    height: auto;
    position: relative;
}
.img{
    width: 70px;
    height: 70px;
    background: url('https://gw.alicdn.com/tfs/TB1p.BGQXXXXXbFXFXXXXXXXXXX-160-160.png');
    background-size: contain;
    background-repeat: no-repeat;
}
#user{
    width:80%;
    height: 40px;
    display: block;
    margin: 20px auto;
    border: none;
    border-bottom: 2px solid #F88400;
    background-color: rgba(255, 255, 255, 0);
} 
#pw{
    width:80%;
    height: 40px;
    display: block;
    margin: 50px auto;
    border: none;
    border-bottom: 2px solid #F88400;
    background-color: rgba(255, 255, 255, 0);
}